//Import Variables //
@import './variables/__all';

//Import Mixins //
@import './mixins/__all';

// Import Utils //
@import  './util/__all';

// Import Components //
@import './components/__all';

// Import Sections //
@import './sections/__all';


body {
    position: relative;
    margin: 0;
    color: #fff;
    font-family: 'Trebuchet', sans-serif;
    font-size: 16px;
    overflow-x: hidden;
    background: rgb(0,132,191);
    background: linear-gradient(90deg, rgba(0,132,191,1) 46%, rgba(128,183,76,1) 70%);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.main-content{
    width: 100%;
    border-radius: 6px;
    background-color: #fff;
    padding: 25px 40px 60px 44px;
}